.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.gpt3__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}
.gpt3__features-container__feature-title h1 {
    color: #FFF;
    font-size: 18px;
    font-family: var(--font-family);
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.96px;
}
.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: linear-gradient(143deg, #AE67FA 0%, #F49867 97.40%), #FFF;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
.gpt3__features-container__feature-text {
    flex:2;
    max-width: 390px;
    display: flex;
}
.gpt3__features-container__feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}
